import Vue from 'vue'
import Vuex from 'vuex'
import { get,set } from '../utils/localStrage.js'
import { login } from '../api/login.js'
Vue.use(Vuex)
let user = get('user') || {};

export default new Vuex.Store({
  state: {
    user,
  },
  getters: {
  },
  mutations: {
    SETUSER(state,data){
      state.user = data;
    },
    REMOVEUSER(state){
      state.user = get('user') || {}
    }
  
  },
  actions: {
    setuser(store,data){
      console.log(data);
      login(data).then(data=>{
        if(data.code == 1){
          let user = {
            token: data.token,
            username: data.username,
          }
          console.log('-----------------------'+user);
          set('user', JSON.stringify(user));
          store.commit('SETUSER',user);
        }
      })
      store.commit('SETUSER',data)
    },
    removeuser(store,data){
      store.commit('REMOVEUSER')
    }
  },
  modules: {

  }
})
