<template>
  <div class="shop" @click="shopClickHandler(shop)">
    <img v-lazy="shop.img_list_url" :key="shop.img_list_url" :style="{ height: 'imgHeight' }" alt="">
    <p class="title">{{ shop.title}} </p>
    <div>
      <span>价格:<span class="price">{{shop.price}} </span>¥</span>
      <p v-html="shop.mack"></p>
    </div>
  </div>
</template>

<script>
import { set } from '../../utils/localStrage'
export default {
  name: "Shop",
  props: {
    shop: Object,
    /**
     * 因为屏幕尺寸不同 每行展示的商品数量不同 所以宽度是变化的
     *
     */
    width: {
      type: String,
      default: "23%",
    },
    imgHeight: {
      type: String,
    },
  },
  methods: {
    shopClickHandler(item) {
      /**
       * 预定义自定义事件 onShopClick
       * 提供点击组件行为
       * 1: 事件对象
       * 2: 被点击的商品数据
       */
      // this.$state.shopId = id
      this.$emit('onShopClick',item)

    },
  },
};
</script>

<style lang='less' scoped>
.shop {
  width: 246px;
  // height: 368px;
  display: inline-block;
  font-size: 12px;
  border: 1px solid #f2f2f2;
  cursor: pointer;
  margin: 10px 0px 15px 0px ;
  background: #F2F6FC;
  vertical-align: top;
  text-align: center;
  padding: 20px 10px;
  &:hover {
    border: 1px solid #303133;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1)
  }
  img {
    width: 198px;
    height: 198px;
    padding-bottom: 20px;
  }
  p {
    font-size: 22px;
    font-weight: 400;
    padding: 15px 5px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  span {
    font-size: 16px;
    font-weight: 300;
    .price {
      font-size: 18px;
      font-weight: 500;
      padding: 0px 3px;
    }
  }
}
</style>
