<template>
  <nav ref="nav" :class="{ navFixed: isFixed }">
    <div class="nav">
      <!-- 搜索 -->
      <div>
        <div class="logo"><img src="../assets/images/logo.png" /></div>
        <div :class="{ searchFixed: isFixed }">
          <search />
        </div>
      </div>
      <!-- 导航 -->
      <ul>
        <li>
          <router-link to="/home">首页</router-link>
        </li>
        <li>
          <!-- <router-link to="/shopcar" v-if="isToken">购物车</router-link> -->
          <router-link to="/shopcar" v-if="$store.state.user.token"
            >购物车</router-link
          >
          <router-link to="/login" v-else>登录</router-link>
        </li>
        <li>
          <!-- <router-link to="/">退出登录</router-link> -->
          <a class="" v-if="$store.state.user.token" @click="logOut"
            >退出登录</a
          >
          <router-link v-else to="/register">注册</router-link>
        </li>
      </ul>
    </div>
  </nav>
</template>

<script>
import Search from "./Search.vue";
import { remove } from "../utils/localStrage.js";
export default {
  components: { Search },
  name: "Nav",
  data() {
    return {
      isFixed: false,
      isNavShow: true,
    };
  },
  created() {},
  mounted() {
    let NavTop = this.$refs["nav"];
    let h = parseFloat(getComputedStyle(NavTop)["height"]);
    let PT = parseFloat(getComputedStyle(NavTop)["paddingTop"]);
    let PB = parseFloat(getComputedStyle(NavTop)["paddingBottom"]);
    let H = h + PT + PB;
    window.addEventListener("scroll", (e) => {
      if (scrollY >= H + 10) {
        this.isFixed = true;
      }
      if (scrollY <= H) {
        this.isFixed = false;
      }
    });
  },
  methods: {
    logOut() {
      // 提示是否推出登录
      this.$confirm("是否退出登录?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "退出登录成功!",
          });
          //点击确认推迟登录
          remove("user");
          // this.$router.push("/home");
          this.$store.dispatch("removeuser");
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消退出登录",
          });
        });
    },
  },
};
</script>

<style scoped lang='less'>
nav {
  z-index: 25;
  width: 100%;
  // height: 150px;
  background-color: #fff;
  padding-bottom: 10px;
  box-shadow: 0 -5px 5px 10px rgba(0, 0, 0, 0.2);
  .nav {
    width: 1200px;
    margin: 0 auto;
    & > div {
      width: 100%;
      position: relative;
      padding: 20px 0 10px 0;
      height: 105px;
      .logo {
        float: left;
        & img {
          width: 188px;
        }
      }
    }
    ul {
      text-align: left;
      li {
        display: inline-block;
        font-weight: 700;
        text-align: center;
        margin: 0 16px;
        cursor: pointer;
        a {
          display: inline-block;
          width: 100%;
          padding: 2px 12px;
          color: #000;
          transition: all 0.3s;
          border-bottom: 2px solid transparent;
          &:hover {
            color: #800080;
            border-bottom: 2px solid #800080;
            text-decoration: none;
          }
        }
      }
    }
  }
}

.navFixed {
  position: sticky;
  left: 0;
  right: 0;
  top: -94px;
}
.searchFixed {
  position: relative;
  top: 60px;
}
.router-link-active {
  color: #800080 !important;
  border-bottom: 2px solid #800080 !important;
}
a:hover {
  color: #800080 !important;
  border-bottom: 2px solid #800080 !important;
}
.navLi-border-bt {
  border-bottom: 2px solid #800080 !important;
}

.navA-color {
  color: #800080;
}
</style>
