import axios from 'axios';
import {
    Loading,
    Message
} from 'element-ui';
import Vue from 'vue';
import {
    set,
} from "./localStrage.js"
import store from '../store/index.js'
import router from '../router/index.js'

let instance = axios.create({
    baseURL: process.env.VUE_APP_BASE_URL,
    timeout: 5000, // 5s 后没有响应认为失败
})

let load = null
let loadServe = null
instance.interceptors.request.use((config) => {
    // config 请求报文配置项目
    // 这里可以修改每个接口的配置项
    // 一般配置统一请求
    // 方式一
    // load = Vue.prototype.$loading({
    //     lock: true,
    //     text: 'Loading',
    //     spinner: 'el-icon-loading',
    //     background: 'rgba(0, 0, 0, 0.7)'
    // });
    //方式二
    loadServe = Loading.service({
        lock: true,
        text: '加载中...',
        spinner: 'el-icon-loading',
        background: 'rgba(0,0,0,0.7)'
    })
    return config //发送请求 没有return 会停止发送
})

// 响应拦截器 服务器响应之后组件接收到响应之前执行 
instance.interceptors.response.use(config => {
    // config 响应报文
    // 统一拦截响应状态 处理不同状态码
    // console.log(`<sapn>服务器响应数据</span>`, config.data);
    // 拦截token 状态 过期跳转登录页面重新登录
    if (config.data.code === 0) {
        // this.$router.push('/login')

        set('user', JSON.stringify({}));
        store.dispatch('removeuser')
        Message({
            message: '请求错误',
            type: 'danger'
        })
        loadServe.close()
        // router.replace('/login').
    } else if (config.data.code === 2) {
        // 统一提示请求失败
        Message({
            message: '请求失败',
            type: 'danger'
        })
        loadServe.close()

    } else {
        setTimeout(() => {
            // load.close();
            loadServe.close();
        }, 1000);
        return config.data //服务器响应的数据
    }
    setTimeout(() => {
        // load.close();
        loadServe.close();
    }, 1000);
    return config.data //服务器响应的数据
}, err => {
    // 服务器超过5s没有响应或者连接问题
    return err;
})

export default instance