<template>
  <div class="search" ref="search">
    <input
      type="text"
      id="search"
      placeholder="请输入想要的商品"
      v-model="keywords"
    />
    <input
      type="submit"
      value="搜索"
      @click="searchLink"
    />
  </div>
</template>

<script>
export default {
  name: "Search",
  data() {
    return {
      keywords: "",
    };
  },
  methods: {
    searchLink(){
      this.$router.push('/search?keyword=' + this.keywords)
    }
  },

};
</script>

<style lang='less' scoped>
.search {
  float: right;
  position: relative;
  transform: translateX(-100px) translateY(30px);
  input {
    &:nth-child(1) {
      width: 480px;
      height: 40px;
      font-size: 13px;
      padding-left: 20px;
      border: 1px solid #dfdfdf;
      border-radius: 5px;
      transition: all 0.2s;
      z-index: 20;
    }
    &:nth-child(1):hover {
      border-color: #aaaaaa;
    }
    &:nth-child(1):focus {
      border-color: #c259c5;
    }
    &:nth-child(2) {
      cursor: pointer;
      position: absolute;
      right: 0;
      top: 1px;
      width: 90px;
      height: 40px;
      border: 1px solid #dfdfdf;
      color: #838383;
      font-size: 13px;
      background-color: #fff;
      border-radius: 0 5px 5px 0;
      transition: all 0.2s;
    }
    &:nth-child(2):hover {
      background-color: rgba(194, 89, 197, 0.1);
      color: rgba(194, 89, 197, 0.8);
      border-color: rgba(194, 89, 197, 0.8);
    }
  }
}
</style>